<template>
<div class="addresspageDiv">
<b class="adp">地址管理</b>
<hr>
            <template v-for="address in addressList">
              <Card class='addresspageDiv-card' :key="address.addressId">
                <p slot="title">{{address.userName}}</p>
                <a slot='extra' @click="updateButton(address)">修改</a>
                 <Divider type="vertical" slot='extra'/>
                 <a slot='extra' @click="delOneButton(address.addressId)">删除</a>
                <p>{{address.userAddress}}</p>
                <p>电话:{{address.userPhone}}</p>
              </Card>  
            </template>
            <br>
           <Button type="primary" @click="addButton">添加新地址</Button>
    <Modal
        v-model="modal1"
        :title="modalTitle"
        @on-ok="addok"
        @on-cancel="addcancel">
        <Form :model="formLeft" label-position="left" :label-width="100">
        <FormItem label="姓名">
            <Input v-model="formLeft.userName"></Input>
        </FormItem>
        <FormItem label="地址">
            <Input v-model="formLeft.address"></Input>
        </FormItem>
        <FormItem label="电话">
            <Input v-model="formLeft.userPhone"></Input>            
        </FormItem>
    </Form>
    </Modal>
</div>  
</template>
<script>
import{addOneAddress,selectAddressMany,delOneAddress,updateOneAddress}from '@/api/api.js'
import { setTimeout } from 'timers';
export default {
    data(){
        return{
            updateNow:false,
            modalTitle:'添加新地址',
            addressList:[],
            modal1:false,
             formLeft: {
                    userName: '',
                    address: '',
                   userPhone: '',
                   userEmail:'',
                   addressId:''
                },
                user:''
        }
    },
    mounted(){
        this.user=JSON.parse(localStorage.getItem('user'))
        let data={userId:this.user.userId}
        selectAddressMany(data).then(res=>{
            this.addressList=res.data.data
           
        })
    },
    methods:{
        //添加地址信息页面
        addButton(){
            this.modal1 = true
            this.modalTitle='添加新地址'
        },
        //修改某个地址信息
        updateButton(obj){
            this.modalTitle='修改地址'         
            this.formLeft.userName=obj.userName
            this.formLeft.address=obj.userAddress
            this.formLeft.userPhone=obj.userPhone
            this.formLeft.userEmail=obj.userEmail
            this.formLeft.addressId=obj.addressId
                this.updateNow=true
             this.modal1=true
        },
        //删除某个地址
        delOneButton(val){
            let data={addressId:val}
            delOneAddress(data).then(res=>{
                 this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'addresspage'})
                   }, 1); 
            })
        },
        addok(){
            if(this.updateNow){
                let data={userName:this.formLeft.userName,userPhone:this.formLeft.userPhone,userEmail:this.formLeft.userEmail,userAddress:this.formLeft.address,userId:this.user.userId,addressId:this.formLeft.addressId}
                updateOneAddress(data).then(res=>{
                this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'addresspage'})
                   }, 10); 
                })
            }else{
                let phone=this.formLeft.userPhone
                if(phone.length!=11){
                    this.$Message.error('手机号格式错误')
                    return
                }
            let data={userName:this.formLeft.userName,userPhone:this.formLeft.userPhone,userEmail:this.formLeft.userEmail,userAddress:this.formLeft.address,userId:this.user.userId}
            addOneAddress(data).then(res=>{
                 this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'addresspage'})
                   }, 1); 
            })
            }
        },
        addcancel(){
            this.formLeft.userName=''
            this.formLeft.userPhone=''
            this.formLeft.userEmail=''
            this.formLeft.address=''
        }
    }
}
</script>
<style lang="less">
.addresspageDiv{
   .adp{
        font-size:30px;
    };
    .addresspageDiv-card{
        width: 250px;
        display:inline-block;
        margin:10px 10px 10px 10px;
    }


}

</style>

